.filters {
    margin-bottom: 2rem;
}

.filters-container {
    border-radius: 50px;
    border: 2px solid #fff;
    width: fit-content;
    padding: .5rem;
    background: linear-gradient(94.43deg, rgba(249, 249, 249, 0.49) 34.95%, rgba(249, 249, 249, 0.175) 67.01%);
    backdrop-filter: blur(10px);
    flex-wrap: wrap;
}

.filters .filter-item {
    margin: 1rem;
    border-radius: 20px;
    width: 180px;
    height: 50px;
    padding: 0.5rem 1rem;
}